.pop {
    position: absolute;
    top: 10px;
    right: 0;
    width: 250px;
    height: 50px;
    padding: 10px;
    border-radius: 4px;
    background-color: #f50057;
    color: white;
  
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-family: 'Roboto', 'Helvetica', 'Arial', sans-serif;
    font-weight: 500;
  }