.enter {
    opacity: 0;
    transform: scale(0);
  }
  .enterActive {
    opacity: 1;
    transform: scale(1);
    transition: all 250ms linear;
  }
  
  .exit {
    opacity: 1;
    transform: scale(1);
  }
  .exitActive {
    opacity: 0;
    transform: scale(0);
    transition: all 250ms linear;
  }