.enter {
    opacity: 0;
    transform: translateX(-100%);
  }
  .enterActive {
    opacity: 1;
    transform: translateX(0);
    transition: all 250ms ease-in;
  }
  
  .exit {
    opacity: 1;
    transform: translateX(0);
  }
  .exitActive {
    opacity: 0;
    transform: translateX(-100%);
    transition: all 250ms ease-out;
  }